<template>
    <div>
        <el-table :data="orderList" border style="width: 100%" :row-class-name="tableRowClassName">
            <el-table-column prop="orderId" label="" v-if="false"></el-table-column>
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="table-expand">
                        <el-form-item label="">
                            <span v-text="translateColor(props.row.productDescription.color)"></span>
                        </el-form-item>
                        <el-form-item label="">
                            <span v-text="props.row.productDescription.outlook"></span>
                        </el-form-item>
                        <el-form-item label="" v-show="hasMemory(props.row)">
                            <span v-text="props.row.productDescription.memory"></span>
                        </el-form-item>
                        <el-form-item label="" v-show="hasStorage(props.row)">
                            <span v-text="props.row.productDescription.storage"></span>
                        </el-form-item>
                        <el-form-item label="">
                            <span v-text="translateAcc(props.row.accessories)"></span>
                        </el-form-item>
                        <el-form-item label="" v-show="hasNote(props.row)">
                            <span v-text="props.row.note"></span>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>
            <el-table-column prop="dateTime" label="" width="160" sortable></el-table-column>
            <el-table-column prop="productName" label="" width="150"></el-table-column>
            <el-table-column prop="productType" label="" :formatter="translateType"></el-table-column>
            <el-table-column prop="purchasePrice" label="" sortable></el-table-column>
            <el-table-column prop="soldPrice" label="" sortable></el-table-column>
            <el-table-column prop="postPrice" label="" sortable></el-table-column>
            <el-table-column prop="profit" label="" sortable></el-table-column>
            <el-table-column prop="purchaser" label=""></el-table-column>
            <el-table-column prop="contact" label="" width="150"></el-table-column>
            <el-table-column prop="platform" label=""></el-table-column>
            <el-table-column fixed="right" label="" width="100">
                <template slot-scope="scope">
                    <el-button type="text" size="small" @click="handleEdit(scope.row)"></el-button>
                    <el-button type="text" style="color:red" size="small" @click="handleDelete(scope.row)">
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next,jumper" :page-size="50" :total="total"
                       :current-page.sync="currentPage" @current-change="changePage">
        </el-pagination>
    </div>
</template>

<script>
    import order from "../../utils/orderRequests"

    export default {
        name: "OrderTable",
        data() {
            return {
                orderList: [],
                currentPage: 1,
                total: 0
            }
        },
        methods: {
            /**
             * valuelabel
             * @param color
             * @returns {any}
             */
            translateColor(color) {
                let colorMap = new Map();
                colorMap.set("red", "");
                colorMap.set("blue", "");
                colorMap.set("green", "");
                colorMap.set("black", "");
                colorMap.set("silver", "");
                colorMap.set("gold", "");
                colorMap.set("yellow", "");
                colorMap.set("white", "");
                colorMap.set("purple", "");
                return colorMap.get(color);
            },

            /**
             * value
             * @param acc
             * @returns {string | *}
             */
            translateAcc(acc) {
                if (!acc) {
                    return ""
                }
                let accMap = {
                    "Charger": "",
                    "Mouse": "",
                    "KeyBoard": "",
                    "Pen": "",
                    "Earphone": "",
                    "Other": "",
                };
                return acc.map(element => {
                    element = accMap[element];
                    return element;
                }).join("");
            },

            translateType(row) {
                let typeMap = {
                    "Phone": "",
                    "Pad": "",
                    "Computer": "",
                    "Accessories": "",
                    "EarPhones": "",
                    "Other": "",
                    "Laptop": "",
                    "Desktop": "",
                    "Apple": "",
                    "Android": "",
                    "Windows": "Windows",
                    "Noiseless": "",
                    "Noise": "",
                    "Wired": "",
                    "Wireless": ""
                };
                if (row.productType.length === 1) {
                    return typeMap[row.productType[0]]
                }
                if (row.productType.length === 2) {
                    return typeMap[row.productType[1]] + typeMap[row.productType[0]]
                }
                return typeMap[row.productType[1]] + typeMap[row.productType[2]] + typeMap[row.productType[0]];
            },

            hasNote(row) {
                return !!row.note;
            },
            hasMemory(row) {
                return row.productType === "Phone" || row.productType === "Computer";
            },
            hasStorage(row) {
                return row.productType === "Phone" || row.productType === "Computer" || row.productType === "Pad";
            },

            /**
             * 
             * @param row
             * @returns {string}
             */
            tableRowClassName({row}) {
                //50
                if (row.profit < 100) {
                    return "loss-row"
                }
                //100
                if (row.profit > 200) {
                    return "earn-row"
                }
                return ""
            },

            handleEdit(row) {
                this.$confirm("", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                    .then(() => {
                        this.$router.push({path: "/dashboard/editOrder", query: {orderId: row.orderId}});
                    })
                    .catch(e => {
                        console.error(e);
                    })
            },
            handleDelete(row) {
                this.$confirm("?", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                    .then(() => {
                        order.deleteOrder("/api/delOrder", {orderId: row.orderId});
                        window.location.reload();
                    })
                    .catch((e) => {
                        console.error(e)
                    })
            },
            async changePage(currentPage) {
                this.currentPage = currentPage;
                await this.axios.post("/api/getOrder", {page: currentPage})
                    .then(response => {
                        this.orderList = response.data.orderList;
                        console.log("");
                    })
                    .catch(e => {
                        this.$message.error("");
                        console.error(e);
                    });
            }
        },
        async mounted() {
            await this.axios.post("/api/getOrder", {page: 1})
                .then(response => {
                    this.orderList = response.data.orderList;
                    this.total = response.data.total;
                    console.log("");
                })
                .catch(e => {
                    this.$message.error("");
                    console.error(e);
                });
        }
    }
</script>

<style>
    .el-table .loss-row {
        background: #fef0f0;
    }

    .el-table .earn-row {
        background: #f0f9eb;
    }

    .table-expand {
        font-size: 0;
    }

    .table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
</style>