<template>
    <el-form :model="profile" label-width="120px" label-position="right" ref="profile" :rules="profileRules"
             v-loading="loading">
        <el-form-item label="" prop="username">
            <el-input v-model="profile.username" style="width:50%"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
            <el-input type="email" v-model="profile.email" class="input_width" required></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-switch v-model="profile.changePassword"></el-switch>
        </el-form-item>
                <el-form-item label="" prop="oldPassword" :hide-required-asterisk="!profile.changePassword">
                    <el-input type="password" v-model="profile.oldPassword" class="input_width"
                              :disabled="!profile.changePassword" show-password></el-input>
                </el-form-item>
                <el-form-item label="" prop="newPassword" :hide-required-asterisk="!profile.changePassword">
                    <el-input type="password" v-model="profile.newPassword" class="input_width"
                              :disabled="!profile.changePassword" show-password minlength="6" maxlength="26"
                              show-word-limit></el-input>
                </el-form-item>
                <el-form-item label="" prop="vnewPassword" :hide-required-asterisk="!profile.changePassword">
                    <el-input type="password" v-model="profile.vnewPassword" class="input_width"
                              :disabled="!profile.changePassword" show-password></el-input>
                </el-form-item>
        <el-row :gutter="6">
            <!---->
            <el-col :span="6">
                <el-form-item>
                    <el-button type="primary" @click.prevent="handleUpdate"></el-button>
                </el-form-item>
            </el-col>
            <el-col :span="8" :offset="3">
                <!---->
                <el-form-item>
                    <el-button type="warning" @click.prevent="handleLogout"
                               v-loading.fullscreen.lock="fullscreenLoading"
                               element-loading-text=".." element-loading-spinner="el-icon-loading"
                               element-loading-background="rgba(0,0,0,0.8)">
                    </el-button>
                </el-form-item>
            </el-col>
        </el-row>
    </el-form>
</template>

<script>
    export default {
        name: "AboutMe",
        data() {
            const validOldPass = (rule, value, callback) => {
                if (!this.profile.changePassword) {
                    callback();
                }
                if (!value) {
                    callback(new Error(""));
                } else {
                    callback();
                }
            };

            const validNewPass = (rule, value, callback) => {
                if (!this.profile.changePassword) {
                    callback();
                }
                if (!value) {
                    callback(new Error(""));
                } else if (value.length < 6 || value.length > 26) {
                    callback(new Error("6-26."));
                } else {
                    callback();
                }
            };

            const validPass = (rule, value, callback) => {
                if (!this.profile.changePassword) {
                    callback();
                }
                if (!value) {
                    callback(new Error(""));
                } else if (value !== this.profile.newPassword) {
                    callback(new Error("!"));
                } else {
                    callback();
                }
            };

            return {
                loading: false,
                fullscreenLoading: false,
                profile: {
                    username: "",
                    email: "",
                    changePassword: false,
                    oldPassword: "",
                    newPassword: "",
                    vnewPassword: "",
                },

                profileRules: {
                    username: [
                        {required: true, message: "", trigger: 'blur'}
                    ],
                    email: [
                        {required: true, message: "", trigger: 'blur'}
                    ],
                    oldPassword: [
                        {validator: validOldPass, trigger: 'blur'}
                    ],
                    newPassword: [
                        {validator: validNewPass, trigger: 'blur'},
                    ],
                    vnewPassword: [
                        {validator: validPass, trigger: 'blur'}
                    ]
                }
            }
        },

        methods: {
            /**
             * 
             * @param api
             * @param data
             * @returns {Promise<void>}
             */
            async editInfo(api, data) {
                await this.axios.put(api, data)
                    .then(response => {
                        if (response.data.status === "success") {
                            this.$message.success("");
                            window.location.reload();
                        } else {
                            this.$message.error(response.data.reason);
                        }
                    })
                    .catch(e => {
                        console.error(e);
                        this.$message.error("")
                    })
            },

            handleUpdate() {
                this.$refs['profile'].validate(valid => {
                    //
                    if (valid) {
                        //
                        this.$confirm("", "", {
                            confirmButtonText: "",
                            cancelButtonText: "",
                            type: "warning"
                        })
                        //
                            .then(() => {
                                this.loading = true;
                                this.editInfo("/api/editUserInfo", this.profile);
                                this.loading = false;
                            })
                            //
                            .catch(() => {
                                this.$message.info("");
                            });
                    }
                    //
                    else {
                        console.warn("");
                        return false;
                    }
                });
            },

            handleLogout() {
                this.$confirm("", "", {
                    confirmButtonText: "",
                    cancelButtonText: "",
                    type: "warning"
                })
                    .then(() => {
                        this.$store.dispatch("logout").catch(e=>console.error(e));
                        this.fullscreenLoading = true;
                        setTimeout(() => {
                            this.fullscreenLoading = false;
                            this.$router.push("/");
                        }, 3000);
                    })
                    .catch(() => {
                        this.$message.info("");
                    })
            }
        },

        mounted() {
            this.axios.get("/api/getUserInfo")
                .then(response => {
                    console.log("");
                    this.profile.username = response.data.username;
                    this.profile.email = response.data.email;
                })
                .catch(e => {
                    console.error(e);
                    this.$message.error("");
                })
        }
    }
</script>

<style scoped>
    .input_width {
        width: 50%;
    }
</style>