<template>
    <el-form-item label="" prop="productType">
        <el-cascader :options="types" :props="expandTrigger" v-model="productType" @change="transferProductType">
        </el-cascader>
    </el-form-item>
</template>

<script>
    export default {
        name: "ProductType",
        data() {
            return {
                productType: [],
                types: [
                    {
                        value: "Phone", label: "", children: [
                            {value: "Apple", label: ""},
                            {value: "Android", label: ""}
                        ]
                    },
                    {value: "Pad", label: ""},
                    {
                        value: "Computer", label: "", children: [
                            {
                                value: "Laptop", label: "", children: [
                                    {value: "Windows", label: "Windows"},
                                    {value: "Apple", label: "Mac"}
                                ]
                            },
                            {
                                value: "Desktop", label: "", children: [
                                    {value: "Windows", label: "Windows"},
                                    {value: "Apple", label: "Mac"}
                                ]
                            }
                        ]
                    },
                    {
                        value: "Accessories", label: "", children: [
                            {value: "Pen", label: ""},
                            {value: "Charger", label: ""},
                            {value: "Mouse", label: ""},
                            {value: "KeyBoard", label: ""}
                        ]
                    },
                    {
                        value: "EarPhone", label: "", children: [
                            {
                                value: "Wireless", label: "", children: [
                                    {value: "Noiseless", label: ""},
                                    {value: "Noise", label: ""},
                                ]
                            },
                            {
                                value: "Wired", label: "", children: [
                                    {value: "Noiseless", label: ""},
                                    {value: "Noise", label: ""},
                                ]
                            }
                        ]
                    },
                    {value: "Other", label: ""}
                ],
                expandTrigger: {expandTrigger: "hover"},
            }
        },
        methods: {
            transferProductType() {
                this.$emit("transferProductType", this.productType)
            }
        },
        props: ["ajaxType"],//EditForm
        watch: {
            ajaxType(val) {
                this.productType = this.productType.concat(val);
            }
        }

    }
</script>

